.app {
  border: solid 10px gray;
  padding: 20px;
}

.resume {
  background-color: white;
  color: black;
}

.avatar {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.avatar-icon {
  color: black;
}

.avatar-photo img {
  vertical-align: middle;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: solid 2px #E5E5E5;
  box-shadow: 2px 2px 5px 0px #00000073;
}

.avatar-information {
  margin-left: 20px;
}

.avatar-information-name {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.avatar-information-phone {
  font-size: 1.3rem;
}

.avatar-information-email {
  color: #5887F5;
  font-size: 1.3rem;
}

.avatar-information-github {
  color: #5887F5;
  font-size: 1.3rem;
}

.avatar-information-quote {
  font-size: 1.1rem;
  margin-top: 10px;
  font-style: italic;
}

.avatar-study {
  font-size: 1.3rem;
  margin-left: 40px;
}

.avatar-study-title {
  font-weight: bold;
}

.avatar-study-idioms {
  font-size: 1.3rem;
  font-weight: bold;
  margin-top: 20px;
}
